/* Base styles */
.App {
  text-align: center;
  justify-content: center;
  height: 100vh;
  margin-top: 0px;
}

h1 {
  font-size: 50px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

span {
  font-size: 55px;
}

button {
  font-size: 20px;
  padding: 7px 15px;
  border-radius: 20px;
  margin: 30px 20px;
  padding: 10px 35px;
  border: none;
  color: black;
}

#stop {
  background-color: red;
}

#start {
  background-color: green;
}

#reset {
  background-color: yellow;
}

.time {
  border-radius: 30px;
  padding: 30px 10px;
  background-color: rgb(214, 211, 198);
  margin: 0 auto;
  width: 400px;
  box-shadow: 2px 2px 10px rgb(97, 95, 95);
}

@media (max-width: 768px) {
  h1 {
    font-size: 30px;
  }

  span {
    font-size: 40px;
  }

  button {
    font-size: 16px;
  }

  .time {
    width: 300px;
    padding: 20px 5px;
  }
}